<template>
  <v-container>
    <v-card
      class="px-5 py-3"
    >
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="desserts"
        :single-select="true"
        item-key="category"
        show-select
        hide-default-footer
        sort-by="category"
      >
        <template
          v-if="dialog"
          v-slot:top
        >
          <v-toolbar
            flat
            color="white"
          >
            <v-spacer></v-spacer>
            <v-dialog
              v-model="dialog"
              max-width="900px"
            >
              <v-card>
                <v-stepper v-model="e1">
                  <v-stepper-header>
                    <v-stepper-step
                      step="1"
                      @click="e1 = 1"
                    >Основная информация</v-stepper-step>

                    <v-divider></v-divider>

                    <v-stepper-step
                      @click="e1 = 2"
                      step="2"
                    >Тарифное условие</v-stepper-step>

                    <v-divider></v-divider>

                    <v-stepper-step
                      @click="e1 = 3"
                      step="3"
                    >Услуги</v-stepper-step>
                  </v-stepper-header>
                  <v-stepper-content
                    step="1"
                  >
                    <v-card-text>
                      <v-container>
                        <v-row>
                          <v-col
                            class="pb-0 pt-0"
                            cols="4"
                          >
                            <p class="title">Категория:</p>
                          </v-col>
                          <v-col
                            class="pb-0 pt-0"
                            cols="8"
                          >
                            <v-select
                              dense
                              v-model="category"
                              :items="categories"
                            />
                          </v-col>
                          <v-col
                            class="pb-0 pt-0"
                            cols="4"
                          >
                            <p class="title">Цена по дням недели:</p>
                          </v-col>
                          <v-col
                            class="pb-0 pt-0"
                            cols="8"
                          >
                            <v-select
                              v-model="daysInWeekTariff"
                              dense
                              :items="daysInWeekTariffs"
                            />
                          </v-col>
                          <v-col>
                            <v-data-table
                              :headers="computedHeaders"
                              :items="computedItems"
                              :items-per-page="5"
                              class="elevation-1"
                            ></v-data-table>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>
                  </v-stepper-content>
                  <v-stepper-content
                    step="2"
                  >
                    <v-card-text>
                      <v-container>
                        <v-row>
                          <v-col
                            class="pb-0 pt-0"
                            cols="4"
                          >
                            <p class="title">Минимальный срок бронирования:</p>
                          </v-col>
                          <v-col
                            class="pb-0 pt-0"
                            cols="8"
                          >
                            <v-text-field
                              v-model="minDays"
                              dense
                            />
                          </v-col>
                          <v-col
                            class="pb-0 pt-0"
                            cols="4"
                          >
                            <p class="title">При предварительном бронировании:</p>
                          </v-col>
                          <v-col
                            class="pb-0 pt-0"
                            cols="8"
                          >
                            <v-text-field
                              v-model="preBooking"
                              dense
                            />
                          </v-col>
                          <v-col
                            class="pb-0 pt-0"
                            cols="4"
                          >
                            <p class="title">Дней/скидка/тип (фикс или процент):</p>
                          </v-col>
                          <v-col
                            class="pb-0 pt-0"
                            cols="8"
                          >
                            <v-text-field
                              v-model="saleType"
                              dense
                            />
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>
                  </v-stepper-content>
                  <v-stepper-content
                    step="3"
                  >
                    <v-card-text>
                      <v-container>
                        <v-row>
                          <v-col
                            class="pb-0 pt-0"
                            cols="4"
                          >
                            <p class="title">Ранний заезд 50% от стоимости:</p>
                          </v-col>
                          <v-col
                            class="pb-0 pt-0"
                            cols="8"
                          >
                            <v-text-field
                              v-model="earlyIn"
                              dense
                            />
                          </v-col>
                          <v-col
                            class="pb-0 pt-0"
                            cols="4"
                          >
                            <p class="title">Поздний выезд 50%:</p>
                          </v-col>
                          <v-col
                            class="pb-0 pt-0"
                            cols="8"
                          >
                            <v-text-field
                              v-model="latelyOut"
                              dense
                            />
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>
                  </v-stepper-content>
                </v-stepper>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="primary"
                    text
                    @click="close"
                  >Отмена</v-btn>
                  <v-btn
                    color="primary"
                    text
                    @click="save"
                  >Сохранить</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
      <v-btn
        @click="dialog=true"
        class="ma-3"
        color="primary"
      >
        Создать тариф
      </v-btn>
    </v-card>
  </v-container>
</template>

<script>
  import Vue from 'vue'
  export default {
    name: 'Tariff',
    data: () => ({
      e1: 1,
      singleSelect: false,
      selected: [],
      dialog: false,
      headers: [
        {
          text: 'Выбрать',
          align: 'start',
          sortable: false,
          value: 'select',
          class: 'table-title',
        },
        { text: 'Категория номера', value: 'category', class: 'table-title' },
        { text: 'Действие', value: 'actions', sortable: false, class: 'table-title' },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        select: '',
        category: 0,
        actions: 0,
      },
      defaultItem: {
        select: '',
        category: 0,
        actions: 0,
      },
      categories: [
        { text: 'Стандарт', value: 'std' },
        { text: 'Полулюкс', value: 'halfLux' },
        { text: 'Люкс', value: 'lux' },
      ],
      daysInWeekTariffs: [
        { text: 'За период', value: 'period' },
        { text: 'По дням ', value: 'days' },
      ],
      category: '',
      daysInWeekTariff: '',
      minDays: '',
      preBooking: '',
      saleType: '',
      earlyIn: '',
      latelyOut: '',
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
      computedHeaders () {
        return this.daysInWeekTariff === 'period' ? this.periodHeaders : this.daysHeaders
      },
      computedItems () {
        return this.daysInWeekTariff === 'period' ? this.periodItems : this.daysItems
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.desserts = [
          {
            select: null,
            category: 159,
          },
        ]
        this.daysHeaders = [
          { text: '', value: 'firstItem' },
          { text: 'ПН', value: 'pn' },
          { text: 'ВТ', value: 'vt' },
          { text: 'СР', value: 'sr' },
          { text: 'ЧТ', value: 'cht' },
          { text: 'ПТ', value: 'pt' },
          { text: 'СБ', value: 'sb' },
          { text: 'ВС', value: 'vs' },
        ]
        this.periodHeaders = [
          { text: 'Название', value: 'name' },
          { text: 'Начало периода', value: 'periodBeg' },
          { text: 'Конец периода', value: 'periodEnd' },
          { text: 'Цена', value: 'price' },
          { text: 'Доп. место', value: 'additionalSeat' },
        ]
        this.daysItems = [
          {
            firstItem: 'Цена',
            pn: '10 000',
            vt: '10 000',
            sr: '10 000',
            cht: '10 000',
            pt: '20 000',
            sb: '20 000',
            vs: '20 000',
          },
          {
            firstItem: 'Доп. место',
            pn: '3 000',
            vt: '3 000',
            sr: '3 000',
            cht: '3 000',
            pt: '5 000',
            sb: '5 000',
            vs: '5 000',
          },
        ]
        this.periodItems = [
          {
            name: 'Июнь',
            periodBeg: '1 июня',
            periodEnd: '30 июня',
            price: '10 000',
            additionalSeat: '3 000',
          },
          {
            name: 'Июль',
            periodBeg: '1 июля',
            periodEnd: '31 июля',
            price: '20 000',
            additionalSeat: '5 000',
          },
        ]
      },
      createTariff () {
        Vue.set(this.desserts, this.desserts.length + 1, this.editedItem)
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.desserts.indexOf(item)
        confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>

<style scoped>
  .title {
    font-size: 18px !important;
    font-weight: 500 !important;
    color: orange !important;
    padding-bottom: 0 !important;
    padding-top: 0 !important;
  }
</style>
